<template>
  <b-card-group class="m-auto w-75">
    <b-card title="Instrues">
      <p class="card-text text-justify">
        Se voc optou por ser citado, voc ter seu nome incluido
        na lista de colaboradores assim que suas alteraes forem aceitas.
      </p>
      <p class="card-text text-justify">
        Fique de olho no seu e-mail, pode ser que entremos em contato
        com voc para averiguar alguma informao provida nas alteraes.
      </p>
      <p class="card-text text-justify">
        Lembrando que aps cada exportao, o banco de dados local  limpado
        para evitar envios duplicados futuramente.
      </p>
      <p class="card-text text-justify">
        Agradecemos seu tempo e disposio para colaborar com o projeto.
      </p>
    </b-card>
    <b-card title="Envio">
      <p class="card-text text-justify">
        Lembre-se de enviar um e-mail com o arquivo gerado em anexo
        para o destinatrio
      </p>
      <pre class="text-center">manga.checklists@gmail.com</pre>
      <p class="card-text text-justify">
        com o assunto <code>[BD] Colaborao</code>.
      </p>
      <p class="card-text text-justify">
        Qualquer observao adicional deve ser enviada juntamente
        ao corpo do e-mail.
      </p>
      <p class="card-text text-justify">
        Pedimos que <em>no</em> renomeie e nem altere com programas externos
        o arquivo gerado.
      </p>
    </b-card>
  </b-card-group>
</template>

<script>
  export default {
    name: 'export-success'
  }
</script>

<style>

</style>



