<template>
  <div class="h-100">
    <app-header title="Incio"></app-header>
    <main role="main">
      <b-container>
        <h4 class="dashboard">Sobre</h4>
        <p class="card-text text-justify">
          Inicialmente, o aplicativo retirava todas as informaes
          disponveis nos sites das editoras, e isso demandava muito
          trabalho e manuteno, por ser um cadastro humano,
          e no terem um padro, na maioria dos casos. Portanto, o 
          aplicativo est passando por um progresso de migrao nos dados, 
          ou seja, parar de pegar as informaes dos sites das editoras e 
          pegar de um "banco de dados" prprio.
        </p>
        <p class="card-text text-justify">
          Isto permitir que o aplicativo tenha dados mais concisos e precisos, 
          alm de poder ter mangs de outras editoras que no disponibilizam
          os checklists em seus sites. Com essa mudana, checklists de editoras
          como a Panini, em que os mangs s so adicionados ao site quando comeam
          a serem vendidos, podero estar no aplicativo completos assim que
          forem disponibilizados.
        </p>
        <p class="card-text text-justify">
          Infelizmente, este processo  demorado no incio, visto que
          so muitos mangs para serem cadastrados. No entanto, assim que as
          informaes antigas forem cadastradas, este cadastro s precisar ser
          feito uma vez por ms, para os checklists novos. Portanto, precisamos
          de sua ajuda para o aplicativo.
        </p>
        <p class="card-text text-justify">
          Esta pgina tem o intuito de ajudar voc a colaborar com a migrao,
          atravs do cadastro dos mangs ainda no disponveis em nosso "banco
          de dados". Aps o processo, voc gerar um arquivo na parte 
          <b-link to="/export">Exportar</b-link> e, seguindo as instrues daquela
          sesso, enviar os dados corretamente para os administradores.
        </p>
        <h4 class="dashboard">Sesses</h4>
        <p class="card-text text-justify">
          Esta pgina  composta de trs sesses inicialmente.
        </p>
        <h5>Checklists</h5>
        <p class="card-text text-justify">
          Aqui os checklists e, consequentemente, seus mangs, so criados.
          Todo o cadastro  manual, ou seja, voc precisa ir nos 
          <a href="https://alessandrojean.github.io/manga-checklists-data/informacoes.html" target="_blank">sites recomendados</a>
          e digitar, ou copiar, as informaes, uma a uma, nos campos. Porm, 
          essa sesso ser automatizada futuramente.
        </p>
        <h5>Comparador</h5>
        <p class="card-text text-justify">
          Aqui voc pode comparar as informaes de um arquivo j gerado
          com os j cadastrados no banco de dados do aplicativo.
        </p>
        <h5>Exportar</h5>
        <p class="card-text text-justify">
          Aqui voc exportar os dados criados para um arquivo que dever
          ser enviado ao e-mail especificado na pgina, como est detalhadamente
          explicado na mesma.
        </p>
      </b-container>
    </main>
  </div>
</template>

<script>
  import AppHeader from './AppHeader'

  export default {
    name: 'landing',
    components: { AppHeader }
  }
</script>

<style>

</style>



